import React,{useEffect, useState} from 'react'
import {HeaderContainer}  from './header'
import {FooterContainer} from './footer'
import Axios from 'axios'

export function MainPage() {
    const [productsrender,setproductsrender]=useState([])
    const [products,setproducts]=useState([])
    const [filteredpro,setfilteredpro]=useState([])
    const [lowerprice,setlowerprice]=useState(0)
    const [higherprice,sethigherprice]=useState(10000)
    const [currentfilters,setcurrentfilters]=useState([])
    useEffect(() => {
        async function products()
        {
        var products2=[]
        var a=await Axios.get('https://5f915976563c230016178be3.mockapi.io/api/v1/products')
        setproducts(a.data)
        setfilteredpro(a.data)
        for(var i=0;i<a.data.length;i++)
        {
        
           products2.push(<div class="Product-card"><div id="grid-items" key={i}>
               
           </div><div class="Product-details">
               <div>{a.data[i]["ProductName"]}</div>
               <div>{a.data[i]["CategoryId"]}</div>
               <div>{a.data[i]["CategoryName"]}</div>
               <div>{a.data[i]["SubCategoryId"]}</div>
               <div>{a.data[i]["SubCategoryName"]}</div>
               <div>{a.data[i]["Price"]}</div>

               </div></div>)
        }
        setproductsrender(products2)
    }
    products()
    }, [])
    function dofilter(filter)
    {
        var a=currentfilters
        var b=[...a,filter]
        setcurrentfilters(b)
        var arr1=[]
for(var i=0;i<products.length;i++)
{
    var e=false
    for(var j=0;j<b.length;j++)
    {
     
       if(b[j]===products[i]["CategoryName"])
       {
         e=true
         break
       }
    }
    if(e===true)
    {
     arr1.push(products[i])
    }
}
        setfilteredpro(arr1)
    render1(arr1)
    }
    function render1(array1)
    {
        console.log(array1)
        var products2=[]
        for(var i=0;i<array1.length;i++)
        {
        
           products2.push(<div class="Product-card"><div id="grid-items" key={i}>
               
           </div><div class="Product-details">
               <div>{array1[i]["ProductName"]}</div>
               <div>{array1[i]["CategoryId"]}</div>
               <div>{array1[i]["CategoryName"]}</div>
               <div>{array1[i]["SubCategoryId"]}</div>
               <div>{array1[i]["SubCategoryName"]}</div>
               <div>{array1[i]["Price"]}</div>

               </div></div>)
        }
   setproductsrender(products2)
    }
 
  function clearfilter()
  {
      setcurrentfilters([])
      setfilteredpro(products)
      render1(products)
  }
  function pricesetter()
  {
      var arr2=filteredpro.filter(ele=>{
        if(ele["Price"]>=lowerprice && ele["Price"]<=higherprice)
        {
         return true
        }
      })
      render1(arr2)
  }
    return (
        <>
        <HeaderContainer/>
        <div id="body">
            <div id="filterbar">
            <div id="filter">
           <div>
               Product Category:
           </div>
           <div id="Product-category">
           <div id="FootWear" onClick={()=>
            {
                document.getElementById('FootWear').style.backgroundColor="lightgray";
                dofilter("FootWear")
           
           }}>FootWear</div>
           <div id="TopWear" onClick={()=>{
               document.getElementById('TopWear').style.backgroundColor="lightgray";
               dofilter("TopWear")}}>TopWear</div>
           <div id="BottomWear" onClick={()=>{
            document.getElementById('BottomWear').style.backgroundColor="lightgray";
               dofilter("BottomWear")}}>BottomWear</div>
           <div id="Clear" onClick={()=>{
                           document.getElementById('BottomWear').style.backgroundColor="white";
                           document.getElementById('TopWear').style.backgroundColor="white";
                           document.getElementById('FootWear').style.backgroundColor="white";

               clearfilter()}}>Clear</div>
            </div>
            <div id="break" ></div>
            <div id="filter" >
                Price:
            <div id="price"><input class="filter-value" value={lowerprice} onChange={({ target }) => setlowerprice(target.value)} />-<input class="filter-value" value={higherprice} onChange={({ target }) => sethigherprice(target.value)}/><div id="Submit" onClick={()=>{pricesetter()}}>Submit</div></div>
          </div>
           </div>
            </div>
       <div id="Main-body">
         {productsrender}
       </div></div>

        <FooterContainer/>
        </>
    )
}
